<template>
  <div id="app">
    <div class="container">
      <div class="scroll-lat">
        <span ref="scrollDot" class="dot"></span>
        <p class="scroll-text">scroll</p>
        <div class="vl"></div>
      </div>
    </div>
  </div>
</template>

<script scoped>
  export default {
    name: 'Social Bar',
    data() {
      return {
        hover_in: false,
        hover_gh: false,
        hover_ig: false,
        hover_be: false,
        hover_sp: false,
      }
    },
    mounted: function() {
      window.addEventListener('scroll', this.handleScroll)
      this.handleScroll();
    },
    methods: {
      handleScroll(){
        var dot = this.$refs.scrollDot;
        
        
        var scrolldistsimple = (document.documentElement.scrollTop / (document.documentElement.scrollHeight - window.innerHeight))*80;
        // OLD SCROLLDISTSIMPLE
        //var scrolldistsimple = (((document.documentElement.scrollTop + 0)*160)/document.documentElement.scrollHeight);
        var scrolldistbottom = (((document.documentElement.scrollTop + window.innerHeight)*80)/document.documentElement.scrollHeight);
        if(document.documentElement.scrollTop<10){
            //when it comes back to the top
            dot.style.transition= "0.4s";
            dot.style.transform = "translateY(0px)";
            dot.style.backgroundColor = 'darkgray';
        } else if(document.documentElement.scrollTop>0 && document.documentElement.scrollTop<10){
            //when it goes from the top
            dot.style.transition= "0.4s";
            dot.style.transform = "translateY(140px)";
        } else if(scrolldistbottom==80){
            //when it hits the bottom
            scrolldistsimple = scrolldistsimple+225;
            dot.style.transition= "0.4s";
            dot.style.transform = "translateY(" + scrolldistsimple + "px)";
            dot.style.backgroundColor = 'darkgray';
        } else{
            //when scrolling in the middle of the page
            scrolldistsimple = scrolldistsimple+140;
            dot.style.transform = "translateY(" + scrolldistsimple + "px)";
            dot.style.backgroundColor = "white";
        }
      },
    }
  }
</script>

<style scoped>
  .dot{
    height: 10px;
    width: 10px;
    background-color: 'darkgray';
    border-radius: 50%;
    display: inline-flex;
  }
  .container{
    z-index:99;
    position: fixed;
    height: 100vh;
    width: 60px;
  }
  .scroll-lat{
    position: absolute;
    top: 45%;
    transform: translateY(-50%);
    opacity: 0.7;
    transition: 0.5s;
    text-align: center;
  }
  .scroll-lat:hover{
    opacity: 1;
    transition: 0.5s;
  }
  .scroll-text{
    transform: rotate(-90deg);
    display: flex;
    margin: 80px 2px 35px 0px;
    opacity: 0.7;
  }
  .vl {
    border-left: 1px solid rgba(255, 255, 255, 0.3);
    height: 80px;
    position: relative;
    left: 50%;
    margin-left: -1px;
    top: 0;
  }
</style>

