import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Working from '../views/Working.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/working',
    name: 'working',
    component: Working
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
